import { computed, inject, reactive } from "vue";
import { useStore } from "vuex";

export function useDialogEmbryoDetailForm() {
    const store = useStore();

    const selectedEmbryo = inject("selectedEmbryo");
    const embryo = inject("embryoDetail");

    const form = reactive({
        fertLineId: null,
        dayNo: 0,
        oocId: null,
        cultureDay: null,
        embryoAge: null,
        stage: null,
        grade: null,
        score: null,
        time: null,
        oocGroup: null,
        clinician: null,
        embryologist: null
    });

    const stageOptions = computed(() => {
        if (selectedEmbryo.day == 0) {
            return store.getters["utils/getMaturityList"];
        } else {
            return store.getters["utils/getFertDay1List"];
        }
    });

    const scoreOpts = computed(() => {
        if (selectedEmbryo.day == 0) {
            return store.getters["utils/getFertCycleType"];
        } else {
            return store.getters["utils/getFertGrade"];
        }
    })

    const clinicianOptions = computed(() => store.state.provider.providers);
    const embryologistOptions = computed(() => store.state.provider.providers);

    return {
        embryo,
        selectedEmbryo,
        stageOptions,
        clinicianOptions,
        embryologistOptions,
        scoreOpts
    };
}
