<template>
    <div class="p-grid">
        <div class="p-col">
            <div class="p-field p-grid">
                <label class="p-col-fixed" style="width: 100px">
                    Incubator
                </label>
                <div class="p-col">
                    <p-input-text
                        v-model="embryo.incubator"
                        style="width: 100%"
                    />
                </div>
            </div>
            <div class="p-field p-grid">
                <label class="p-col-fixed" style="width: 100px">
                    Description
                </label>
                <div class="p-col">
                    <p-textarea
                        v-model="embryo.description"
                        rows="5"
                        style="width: 100%"
                    />
                </div>
            </div>
            <div class="p-field p-grid">
                <label class="p-col-fixed" style="width: 100px">
                    Upload Image
                </label>
                <div class="p-col">
                    <p-file-upload
                        v-if="uploaded"
                        name="demo[]"
                        mode="basic"
                        :customUpload="true"
                        accept="image/*"
                        :auto="true"
                        @uploader="imgUploader"
                    ></p-file-upload>
                </div>
            </div>
        </div>

        <div class="p-col-fixed" style="width: 200px">
            <img :src="image" alt="image" style="width: 150px; height: 150px" />
        </div>

        <div
            class="p-col-fixed"
            style="
                justify-content: end;
                display: flex;
            "
        >
            <table style="max-height: 100px">
                <tr>
                    <td></td>
                    <td>
                        <p-button
                            icon="pi pi-angle-up"
                            @click="onArrowBtnClick('up')"
                        />
                    </td>
                    <td></td>
                </tr>
                <tr>
                    <td>
                        <p-button
                            icon="pi pi-angle-left"
                            @click="onArrowBtnClick('left')"
                        />
                    </td>
                    <td></td>
                    <td>
                        <p-button
                            icon="pi pi-angle-right"
                            @click="onArrowBtnClick('right')"
                        />
                    </td>
                </tr>
                <tr>
                    <td></td>
                    <td>
                        <p-button
                            icon="pi pi-angle-down"
                            @click="onArrowBtnClick('down')"
                        />
                    </td>
                    <td></td>
                </tr>
            </table>
        </div>
    </div>

    <!-- <strong>Protocols</strong>

    <p-data-table
        :value="table"
        dataKey="id"
        class="p-datatable-sm p-shadow-1"
        style="height: 200px"
    >
        <p-column field="date" header="Date" style="width: 100px" />
        <p-column
            field="description"
            header="Description"
            style="width: 300px"
        />
        <p-column field="fileName" header="Filename" />
        <p-column header="Protocol" />
    </p-data-table> -->
</template>

<script>
import { inject, onMounted, ref, watch } from "vue";
import { emitter } from "@/event-bus";
import { ORIGIN } from "@/config";

export default {
    setup() {
        const embryo = inject("embryoDetail");

        const image = ref("https://via.placeholder.com/300");
        const uploaded = ref(true);
        const imgUploader = (event) => {
            uploaded.value = false;
            const reader = new FileReader();
            reader.readAsDataURL(event.files[0]);
            reader.onload = () => {
                embryo.newImage = reader.result;
                image.value = reader.result;
                uploaded.value = true;
            };
            reader.onerror = (error) => console.log(error);
        };

        watch(
            () => embryo.images,
            (val) => {
                if (val.length == 0) {
                    image.value = "https://via.placeholder.com/300";
                } else {
                    image.value = `${ORIGIN}/${val[val.length - 1]}`;
                }
            }
        );

        const onArrowBtnClick = (direction) => {
            emitter.emit("embyro-detail-dialog:move-cell", direction);
        };

        onMounted(() => {
            if (embryo.images.length == 0) {
                image.value = "https://via.placeholder.com/300";
            } else {
                image.value = `${ORIGIN}/${embryo.images[embryo.images.length - 1]}`;
            }
        });

        return { embryo, image, onArrowBtnClick, imgUploader, uploaded };
    }
};
</script>
