    <p-card class="p-mt-1">
        <template #content>
            <p-divider align="left" class="p-mt-0">
                <b>Embryology - Frozen Embryo</b>
            </p-divider>

            <div class="p-grid p-fluid">
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Oocyte Collection Date
                        </label>
                        <div class="p-col">
                            <p-calendar
                                v-model="form.oocyteCollectionDate"
                                dateFormat="yy-mm-dd"
                                :showIcon="true"
                            />
                        </div>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Freeze Id #1 of thawed embryos:
                        </label>
                        <div class="p-col">
                            <p-input-text
                                v-model="form.embryoFreezeID1Thawed"
                            />
                        </div>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Freeze Id #2 of thawed embryos:
                        </label>
                        <div class="p-col">
                            <p-input-text
                                v-model="form.embryoFreezeID2Thawed"
                            />
                        </div>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Freeze Id #3 of thawed embryos:
                        </label>
                        <div class="p-col">
                            <p-input-text
                                v-model="form.embryoFreezeID3Thawed"
                            />
                        </div>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Freeze Id #4 of thawed embryos:
                        </label>
                        <div class="p-col">
                            <p-input-text
                                v-model="form.embryoFreezeID4Thawed"
                            />
                        </div>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="">
                            Freeze Id #4 of thawed embryos:
                        </label>
                        <div class="p-col">
                            <div class="p-formgroup-inline">
                                <div class="p-field-checkbox">
                                    <p-radio-button value="1" />
                                    <label>Yes</label>
                                </div>
                                <div class="p-field-checkbox">
                                    <p-radio-button value="0" />
                                    <label>NO</label>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            # embryo thawed
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.embryosThawed" />
                        </div>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            # utilizable embryos after thaw:
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.utilizableAfterThaw" />
                        </div>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Embryo stage at thawing
                        </label>
                        <div class="p-col">
                            <p-multi-select
                                v-model="form.embryoStageThawing"
                                :options="embryoStageThawingOptions"
                                optionLabel="label"
                                optionValue="value"
                                style="width: 250px"
                            />
                        </div>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Embryo cryo method
                        </label>
                        <div class="p-col">
                            <p-dropdown
                                v-model="form.embryoFrozenCryoMethod"
                                :options="embryoTransferOptions"
                                optionLabel="label"
                                optionValue="value"
                            />
                        </div>
                    </div>
                </div>
            </div>
        </template>

        <template #footer>
            <p-button
                icon="pi pi-save"
                label="Save"
                @click="save"
                :loading="loading"
            />
        </template>
    </p-card>
</template>

<script>
import { onMounted, watch } from "vue";
import { useBronFrozenEmbyoInit } from "@/composable/born";
export default {
    setup() {
        const {
            store,
            loading,
            form,
            save,
            fillForm,
            embryoStageThawingOptions,
            embryoTransferOptions
        } = useBronFrozenEmbyoInit();

        watch(() => store.state.born.frozenEmbryos, fillForm);

        onMounted(() => {
            if (store.state.born.frozenEmbryos == null) {
                store.dispatch(
                    "born/getBronV2",
                    store.state.artCycle.selectedCycle
                );
            } else {
                fillForm(store.state.born.frozenEmbryos);
            }
        });

        return {
            loading,
            form,
            save,
            fillForm,
            embryoStageThawingOptions,
            embryoTransferOptions
        };
    }
};
</script>
